import { CreateTeamDto, Team } from "@/entities/team/model/types";
import { API_BASE_URL } from "@/shared/config/api";

export class TeamsService {
  async createTeam(data: CreateTeamDto, token: string): Promise<Team> {
    console.log("Creating team with data:", data);
    console.log("Using token:", token);

    const response = await fetch(`${API_BASE_URL}/teams`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify(data),
      credentials: "include",
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => null);
      console.error("Error creating team:", {
        status: response.status,
        statusText: response.statusText,
        error: errorData,
      });
      throw new Error(errorData?.detail || "Failed to create team");
    }

    const team = await response.json();
    console.log("Created team:", team);
    return team;
  }

  async getTeamById(id: number, token: string): Promise<Team> {
    console.log(`Fetching team ${id} with token:`, token);

    const response = await fetch(`${API_BASE_URL}/teams/get_by_id/${id}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      credentials: "include",
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => null);
      console.error("Error fetching team:", {
        status: response.status,
        statusText: response.statusText,
        error: errorData,
      });
      throw new Error(errorData?.detail || "Failed to fetch team");
    }

    const team = await response.json();
    console.log("Fetched team:", team);
    return team;
  }

  async getTeamByCode(code: string, token: string): Promise<Team> {
    console.log(`Fetching team by code ${code} with token:`, token);

    const response = await fetch(`${API_BASE_URL}/teams/get_by_code/${code}`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      credentials: "include",
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => null);
      console.error("Error fetching team by code:", {
        status: response.status,
        statusText: response.statusText,
        error: errorData,
      });
      throw new Error(errorData?.detail || "Failed to fetch team");
    }

    const team = await response.json();
    console.log("Fetched team by code:", team);
    return team;
  }

  async joinTeam(teamId: number, token: string): Promise<void> {
    console.log(`Joining team ${teamId} with token:`, token);

    const response = await fetch(`${API_BASE_URL}/teams/join`, {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
        Authorization: `Bearer ${token}`,
      },
      body: JSON.stringify({ team_id: teamId }),
      credentials: "include",
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => null);
      console.error("Error joining team:", {
        status: response.status,
        statusText: response.statusText,
        error: errorData,
      });
      throw new Error(errorData?.detail || "Failed to join team");
    }

    console.log("Successfully joined team");
  }

  async getMyTeams(token: string): Promise<Team[]> {
    console.log("Fetching my teams with token:", token);

    const response = await fetch(`${API_BASE_URL}/teams/my-teams`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      credentials: "include",
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => null);
      console.error("Error fetching teams:", {
        status: response.status,
        statusText: response.statusText,
        error: errorData,
      });
      throw new Error(errorData?.detail || "Failed to fetch teams");
    }

    const teams = await response.json();
    console.log("Fetched teams:", teams);
    return teams;
  }

  async getTeamMembers(teamId: number, token: string): Promise<any[]> {
    console.log(`Fetching members for team ${teamId} with token:`, token);

    const response = await fetch(`${API_BASE_URL}/teams/${teamId}/members`, {
      headers: {
        Authorization: `Bearer ${token}`,
      },
      credentials: "include",
    });

    if (!response.ok) {
      const errorData = await response.json().catch(() => null);
      console.error("Error fetching team members:", {
        status: response.status,
        statusText: response.statusText,
        error: errorData,
      });
      throw new Error(errorData?.detail || "Failed to fetch team members");
    }

    const members = await response.json();
    console.log("Fetched team members:", members);
    return members;
  }
}
