import { useCallback, useState } from "react";
import { useAuthContext } from "@/app/providers/auth-provider";
import { TeamsService } from "../api/teams.service";
import { CreateTeamDto, Team } from "@/entities/team/model/types";
import { useToast } from "@/shared/ui/use-toast";

export function useTeams() {
  const { token } = useAuthContext();
  const { toast } = useToast();
  const [isLoading, setIsLoading] = useState(false);
  const [myTeams, setMyTeams] = useState<Team[]>([]);
  const teamsService = new TeamsService();

  const createTeam = useCallback(
    async (data: CreateTeamDto) => {
      if (!token) {
        throw new Error("No authorization token available");
      }

      setIsLoading(true);
      try {
        const team = await teamsService.createTeam(data, token);
        setMyTeams((prev) => [...prev, team]);
        return team;
      } finally {
        setIsLoading(false);
      }
    },
    [token, teamsService]
  );

  const joinTeam = useCallback(
    async (teamId: number) => {
      if (!token) {
        throw new Error("No authorization token available");
      }

      setIsLoading(true);
      try {
        await teamsService.joinTeam(teamId, token);
        const teams = await teamsService.getMyTeams(token);
        setMyTeams(teams);
      } finally {
        setIsLoading(false);
      }
    },
    [token, teamsService]
  );

  const fetchMyTeams = useCallback(async () => {
    if (!token) {
      throw new Error("No authorization token available");
    }

    setIsLoading(true);
    try {
      const teams = await teamsService.getMyTeams(token);
      setMyTeams(teams);
      return teams;
    } finally {
      setIsLoading(false);
    }
  }, [token, teamsService]);

  const getTeamById = useCallback(
    async (id: number) => {
      if (!token) {
        throw new Error("No authorization token available");
      }

      setIsLoading(true);
      try {
        const team = await teamsService.getTeamById(id, token);
        return team;
      } finally {
        setIsLoading(false);
      }
    },
    [token, teamsService]
  );

  const getTeamByCode = useCallback(
    async (code: string) => {
      if (!token) {
        throw new Error("No authorization token available");
      }

      setIsLoading(true);
      try {
        const team = await teamsService.getTeamByCode(code, token);
        return team;
      } finally {
        setIsLoading(false);
      }
    },
    [token, teamsService]
  );

  const getTeamMembers = useCallback(
    async (teamId: number) => {
      if (!token) {
        throw new Error("No authorization token available");
      }

      setIsLoading(true);
      try {
        const members = await teamsService.getTeamMembers(teamId, token);
        return members;
      } finally {
        setIsLoading(false);
      }
    },
    [token, teamsService]
  );

  return {
    createTeam,
    joinTeam,
    fetchMyTeams,
    getTeamById,
    getTeamByCode,
    getTeamMembers,
    isLoading,
    myTeams,
  };
}
